




































export default {
	data() {
		return {
			list_content: [],
			title_content: []
		};
	},
	methods: {
		top_detail(e) {
			var id = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: "../info/top?id=" + id
			})
		},
		detail(e) {
			uni.navigateTo({
				//https://news-at.zhihu.com/api/3/news/hot
				url: "../info/list?id=" + e.currentTarget.dataset.news_id
			})
		}
	},
	onLoad() {
		uni.showLoading({
			title: "加载中...."
		});

		uni.request({
			url: 'https://news-at.zhihu.com/api/4/news/latest',
			method: 'GET',
			data: {},
			success: res => {
				this.title_content = res.data.top_stories;
			},
			fail: () => {},
			complete: () => {}
		});

		uni.request({
			url: 'https://news-at.zhihu.com/api/3/news/hot',
			method: 'GET',
			data: {},
			success: res => {
				this.list_content = res.data.recent;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		})
	}
}



